.chat-container {
    position: fixed;
    bottom: 20px;
    left: 20px;
    z-index: 999;
  }
  
  /* Nút mở chat */
  .chat-icon {
    background-color: blue;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    font-size: 16px;
  }
  
  /* Form nhập tên */
  .chat-login {
    width: 250px;
    background: white;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  }
  
  .chat-login h2 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .chat-login input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .chat-login button {
    width: 100%;
    background: blue;
    color: white;
    padding: 8px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  /* Khung chat */
  .chat-box {
    width: 300px;
    height: 400px;
    background: white;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    padding: 15px;
    position: relative;
  }
  
  .chat-box h2 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .chat-close {
    position: absolute;
    top: 5px;
    right: 10px;
    background: none;
    border: none;
    color: red;
    font-size: 14px;
    cursor: pointer;
  }
  
  /* Khung hiển thị tin nhắn */
  .chat-messages {
    height: 250px;
    overflow-y: auto;
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 10px;
  }
  
  .chat-message {
    margin-bottom: 5px;
  }
  
  /* Ô nhập tin nhắn */
  .chat-input {
    display: flex;
  }
  
  .chat-input input {
    flex: 1;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .chat-input button {
    background: blue;
    color: white;
    padding: 8px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    margin-left: 5px;
  }
  